
import { reactive, ref, onBeforeUnmount } from "vue";
export default {
  name: "layout",

  setup() {
    const imageList = reactive([
      {
        id: 0,
        url: require("../../assets/bg/IMG_20210220_183701.jpg"),
      },
      {
        id: 1,
        url: require("../../assets/bg/code.png"),
      },
      { id: 2, url: require("../../assets/bg/i1.jpg") },
    ]);

    let activeid = ref<number>(0);

    let timer = setInterval((): void => {
      if (activeid.value >= imageList.length - 1) {
        activeid.value = 0;
      } else {
        activeid.value++;
      }
    }, 3000);

    onBeforeUnmount((): void => {
      clearInterval(timer);
    });

    return { imageList, activeid };
  },
};
